}
.test__button:hover {
  background-color: #e7e7ff;
}
.test__button--wrong {
  background: #eb6465;
}
.test__button--wrong:hover {
  background-color: #ee7b7b;
}
.test__button--correct {
  background: #b7ff8d;
}
.test__button--correct:hover {
  background-color: #c7ffa7;
}
.test__button--passive {
  background: #e4e4e4;
}
.test__button--passive:hover {
  background-color: #f1f1f1;
}

