  align-self: flex-end;
  padding: 10px;
  background: #b7ff8d;
  border-radius: 5px;
  transition: 0.3s all ease-in;
  font-size: 16px;
  line-height: 24px;
}
.homonyms-puzzles__answer:hover {
  background: #97ff5a;
}
.homonyms-puzzles__answer--active {
  background: ligten(#b7ff8d, 10%);
}
@media (min-width: 768px) {
  .homonyms-puzzles__answer {
    font-size: 19px;
    line-height: 28.5px;
  }
}
@media (min-width: 1260px) {
  .homonyms-puzzles__answer {
    font-size: 22px;
    line-height: 33px;
    align-self: center;
  }
}

.homonyms-examples__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 15px;
}
@media (min-width: 768px) {
  .homonyms-examples__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .homonyms-examples__list {
    gap: 25px;
    width: 90%;
    margin: 0 auto;
  }
}

.homonyms-examples__example {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffcdcd;
  border-radius: 10px;
  padding: 20px 0;
}

.homonyms-examples__name {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 26px;
  font-weight: 700;
}
@media (min-width: 768px) {
  .homonyms-examples__name {
    font-size: 24px;
    line-height: 36px;
  }
}
@media (min-width: 1260px) {
  .homonyms-examples__name {
    font-size: 26px;
    line-height: 39px;
  }
}

.homonyms-examples__image {
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .homonyms-examples__image {
    width: 190px;
    height: 95px;
  }
}
@media (min-width: 1260px) {
  .homonyms-examples__image {
    width: 290px;
    height: 145px;
    margin-bottom: 20px;
  }
}

@media (min-width: 1260px) {
  .paronyms-about__paragraph {
    width: 80%;
    margin-bottom: 5px;
  }
}

.paronyms-about__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 5px;
}

.paronyms-about__item {
  position: relative;
  font-size: 18px;
  line-height: 24px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .paronyms-about__item {
    font-size: 22px;
    line-height: 33px;
    padding-left: 25px;
  }
}
@media (min-width: 1260px) {
  .paronyms-about__item {
    font-size: 27px;
    line-height: 40.5px;
    padding-left: 30px;
  }
}
.paronyms-about__item:nth-child(1)::before {
  position: absolute;
  content: "а) ";
  color: #ff498b;
  left: 0;
}
.paronyms-about__item:nth-child(2)::before {
  position: absolute;
  content: "б) ";
  color: #ff498b;
  left: 0;
}
.paronyms-about__item:nth-child(3)::before {
  position: absolute;
  content: "в) ";
  color: #ff498b;
  left: 0;
}

.paronyms-examples__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  display: grid;
  gap: 10px;
}
@media (min-width: 768px) {
  .paronyms-examples__list {
    width: 90%;
    margin: 0 auto;
  }
}
@media (min-width: 1260px) {
  .paronyms-examples__list {
    grid-template-columns: repeat(2, 1fr);
  }
}

.paronyms-examples__item {
  padding: 7px;
  background: #cfcdff;
  border-radius: 5px;
}
@media (min-width: 1260px) {
  .paronyms-examples__item:last-child {
    grid-column: 1/3;
    margin: 0 auto;
    width: 48%;
  }
}

.pseudonyms-about__paragraph {
  width: 80%;
