  font-size: 15px;
  border-radius: 15px;
  text-align: center;
  margin: 0 auto;
  width: 100%;
}
.pseudonyms-classification__table th {
  background: #cfcdff;
  padding: 10px;
}
@media (min-width: 768px) {
  .pseudonyms-classification__table {
    font-size: 20px;
    width: 100%;
  }
}
@media (min-width: 1260px) {
  .pseudonyms-classification__table {
    width: 80%;
  }
}
.pseudonyms-classification__table th:first-child {
  border-top-left-radius: 15px;
}
.pseudonyms-classification__table th:last-child {
  border-top-right-radius: 15px;
}

.pseudonyms-classification__tr {
  background: #f6f6f6;
  height: 30px;
}

.pseudonyms-classification__td {
  padding: 10px;
  border: 2px solid #ffffff;
